.main{
    width: 100%;
}
.main__container{
    max-width: 998px;
    width: 100%;
    border: 1px solid black;
    margin: 40px auto;
    min-height: 700px;
}
.main__button-container{
    border-bottom: 1px solid black;
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
}
.main__cards{
    display: flex;
    flex-direction: column;
}
.main__card{
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid black;
    align-items: center;
}
.main__card-button{
    font-size: 3rem;
    border: none;
    background: transparent;
    cursor: pointer;
    transform: rotate(0);
    transition: all .35s;
}
.main__card-button:hover{
    transform: rotate(180deg);
    transition: all .35s;
}
.main__card-button p{
    transform: rotate(45deg);
    margin: 0;
}
.main__tabs-container{
    display: flex;
    border-bottom: 1px solid black;
}
.main__tab{
    width: 100%;
    border: none;
    padding: 25px 0;
    font-size: 23px;
    background: transparent;
    cursor: pointer;
    transition: all .6s;
}
.main__tab:hover{
    background-color: cadetblue;
    color: white;
}
.main__tab:first-of-type{
    border-right: 1px solid black;
}
.main__tab.active{
    background-color: cadetblue;
    color: white;
}

@media screen and (max-width: 998px) {
    .main__tabs-container{
        flex-direction: column;
    }
}
